.Modal {
  width: 90vw;
  height: 70vh;
  background-color: black;
  flex: 1;
  flex-direction: row;
  position: absolute;
  border-style: solid;
  border-width: 0.7;
  border-color: black;
  border-radius: 20px;
  box-shadow: 0px 0px 5px 1px #414141;
}

.ModalClose {
  margin: 10px;
  text-align: end;
  position: sticky;
}

.ModalShown {
  visibility: visible;
}

.ModalHidden {
  visibility: hidden;
}
